import * as yup from 'yup'

export const schemaYup = yup.object().shape({
  incidente: yup.object({
    value: yup.string(),
    label: yup.string(),
  }).default(null).nullable().required('El Incidente es obligatorio'),
  categoria: yup.object({
    value: yup.string(),
    label: yup.string(),
  }).default(null).nullable().required('La Categora es obligatoria'),
  subCategoria: yup.object({
    value: yup.string(),
    label: yup.string(),
  }).default(null).nullable().required('La Sub-Categora es obligatoria'),
  institucion: yup.string().required('La Institucin es obligatoria'),
  cedula: yup.string()
    .required('La Cdula es obligatoria')
    .length(11, 'Debe tener exactamente 11 dgitos'),
  telefono: yup.string()
    .required('El Telfono es obligatorio')
    .length(10, 'Debe tener exactamente 10 dgitos'),
  region: yup.string().required('La Regin es obligatoria'),
  provincia: yup.string().required('La Provincia es obligatoria'),
  municipio: yup.string().required('El Municipio es obligatorio'),
  distrito: yup.string().required('El Distrito es obligatorio'),
  seccion: yup.string().required('La Seccin es obligatoria'),
  barrio: yup.object({
    value: yup.string(),
    label: yup.string(),
  }).default(null).nullable().required('El Barrio es obligatorio'),
  subBarrio: yup.object({
    value: yup.string(),
    label: yup.string(),
  }).default(null).nullable(),
// }).default(null).nullable().required('El Sub-Barrio es obligatorio'),
  direccion: yup.string().trim()
    .required('La Direccin es obligatoria'),
  descripcion: yup.string().trim()
    .required('La Descripcin es obligatoria'),
})